	module.exports = {"data":{"markdownRemark":{"html":"<p>This Victorian School was converted into an impressive seven bedroom family home. We started work on the project before exchange of contracts. The original owner/developer had designed the kitchen but the buyers, our clients, were not happy with the layout so we were asked to redesign the kitchen and carried on to design the whole house.</p>\n<p>Some of the original school halls were converted into two floors leaving interesting windows providing a challenge for window dressings.</p>\n<p>We designed bespoke furniture for many areas including, a dining table and display cabinets, oak bookcases and plasma TV housing in the family room, and a concave oak storage unit in the sitting room.</p>","frontmatter":{"date":"January 01, 2004","slug":"2004-converted-victorian-school","title":"Converted Victorian School near Winchester","cover":"dom5i.jpg"}},"galleriesJson":{"images":[{"title":"Bespoke kitchen with large island","path":"dom5a.jpg"},{"title":"Bespoke kitchen with range cooker","path":"dom5l.jpg"},{"title":"Dining hall with bespoke furniture","path":"dom5d.jpg"},{"title":"Lighting feature in dining hall","path":"dom5c.jpg"},{"title":"Family room seating area","path":"dom5g.jpg"},{"title":"Family room bespoke bookcase","path":"dom5f.jpg"},{"title":"Sitting room with large bespoke rug","path":"dom5i.jpg"},{"title":"Sitting room window treatments","path":"dom5h.jpg"},{"title":"Sitting room corner unit","path":"dom5j.jpg"},{"title":"Bespoke shutters in bathroom","path":"dom5e.jpg"},{"title":"Dressing table in master bedroom","path":"dom5k.jpg"}]}},"pathContext":{"slug":"2004-converted-victorian-school"}}

